import Vue from 'vue';
import Router from 'vue-router';

// Investor Portal
import InvestorHeading from '@/components/InvestorHeading.vue';
import InvestorDashboard from '@/components/InvestorDashboard.vue';

// Research Portal
import ResearchHeading from '@/components/ResearchHeading.vue';
import ResearchDashboard from '@/components/ResearchDashboard.vue';

Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/investor',
      name: 'Investor',
      meta: { layout: 'investor' },
      component: InvestorHeading,
      children: [
        {
          path: 'dashboard',
          name: 'Dashboard',
          meta: { layout: 'investor' },
          component: InvestorDashboard,
        },
      ],
    },
    {
      path: '/research',
      name: 'Research',
      meta: { layout: 'research' },
      component: ResearchHeading,
      children: [
        {
          path: 'dashboard',
          name: 'Dashboard',
          meta: { layout: 'research' },
          component: ResearchDashboard,
        },
      ],
    },
  ],
});
